import { useState } from "react";
const { Card } = window.KmtAdminComponents;
const { __ } = wp.i18n;
const { Dashicon } = wp.components;
const SingleOption = (props) => {
    const [value, setValue] = useState(props.value);
    const [isLoading, setIsLoading] = useState(false);

    const handleChange = async () => {
        setIsLoading(true);
        let newValue = !value
        const body = new FormData()
        body.append('action', 'kemet-panel-update-option')
        body.append('nonce', KemetAddonsPanelData.nonce)
        body.append('option', props.id)
        body.append('value', newValue)

        try {
            const response = await fetch(KemetAddonsPanelData.ajaxurl, {
                method: 'POST',
                body,
            })
            if (response.status === 200) {
                const { success, data } = await response.json()
                if (success && data.values) {
                    setValue(newValue);
                    props.onChange(data.values);
                }
            }
        } catch (e) {
            console.log(e);
        }
        setIsLoading(false)
    };

    let btnText = value === true ? __('Deactivate', 'kemet-addons') : __('Activate', 'kemet-addons')
    const btnClasses = value === true ? 'secondary' : 'primary';
    return <Card id={props.id}>
        <label>
            <span className="customize-control-title kmt-control-title">{props.params.label}</span>
            <div className="description customize-control-description">{props.params.description}</div>
            {isLoading && (
                <Dashicon className='kmt-loading' icon='update' />
            )}
        </label>
        <div className="option-actions">
            <button className={`kmt-button ${btnClasses}`} onClick={() => {
                handleChange()
            }} disabled={isLoading}>{btnText}</button>
            {value && props.params.url && <a className='kmt-button' href={props.params.url}>{__('Customize', 'kemet-addons')}</a>}
        </div>
    </Card>
}

export default SingleOption